import firebase from 'firebase';
import app from './firebase';


//TODO generate token for moodle webservice and insert to Course (webservice/lib)
export const addCourseCommand = async (image,user, data) => {

  // const { userapi} = useAuth();
  try {
    let command={
      type:"course",
      action:"add",
      status:0,
      user:user,
      data:data
    }
    console.log(command);
    let courseApi = await app.firestore().collection('commands').add(command);
    let course = await courseApi.get();
    let coursedata = course.data();
    var metadata = {
      contentType: 'image/jpeg'
    };
    let fb_url = "public/"+data.school.id+"/course/" + course.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(fb_url).put(image, metadata);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
      function (snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload is running');
            break;
        }
      },
      function (error) {

        switch (error.code) {
          case 'storage/unauthorized':
              console.log("unauthorized")
            break;
          case 'storage/canceled':
            console.log("cancel")
            break;
          case 'storage/unknown':

            console.log("unkonwn")
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function () {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
          console.log('File available at', downloadURL);
          coursedata.data.image = downloadURL;
          app.firestore().collection('commands').doc(course.id).set(coursedata);
        });
      });

  } catch (err) {
    console.log(err);
  }
};

export const getCourseApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('courses').doc(id).get();
    const course = querySnapshot.data()
    course.id = id
    return course
  } catch (err) {
    console.log(err);
  }
}

export const updateCourse = async (data) => {
  try {
    let id = data.id;
    let result = await app.firestore().collection('courses').doc(id).set(data);
    return result
  } catch (err) {
    console.log(err);
  }
};

export const listAllCourses = async (data) => {
  try {
    let querySnapshot = await app.firestore().collection('courses').get();
    const course = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      }
    })
    return course
  } catch (err) {
    console.log(err);
  }
}
